.contact {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: 80px;
    margin: 80px 170px;
}

.contact-title {
    position: relative;
}

.contact-title h1{
    padding: 0px 30px;
    font-size:80px ;
    font-weight: 600;
}

.contact-title img {
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: -1;
}


.contact-section {
    display: flex;
    flex-wrap: wrap;
    gap: 150px;
}

.contact-left {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 30px;
}

.contact-left h1 {
    font-size: 80px;
    font-weight: 700;
    background: linear-gradient(270deg #DF8908 80%, #B415FF 100%);
    background-clip: text;
    -webkit-background-clip: text;
}

.contact-left p {
     max-width: 550px;
     color: #D8D8D8;
     font-size: 20px;
     line-height: 35px;
}

.contact-details {
      display: flex;
      flex-direction: column;
      gap: 30px;
      color: #D8D8D8;
      font-size:22px ;
}

.contact-detail {
    display: flex;
    align-items: center;
    gap:20px ;
}

.contact-right {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: start;
    gap: 30px;
}

.contact-right label {
    color: #D8D8D8;
    font-size: 22px;
    font-weight: 500;
}

.contact-right input {
    border: none;
    width: 100%;
    height: 78px;
    padding-left: 20px;
    border-radius: 4px;
    background: #32323c;
    color: #A0A0A0;
    font-family: Outfit;
    font-size: 20px;
}

.contact-right textarea {
      width: 100%;
      border: none;
      padding: 25px;
      border-radius: 4px;
      background: #32323c;
      color: #A0A0A0;
      font-family: Outfit;
      font-size: 20px;
}

.contact-submit {
    border: none;
    color: white;
    border-radius: 50px;
    background: linear-gradient(264deg, #DF8908 -5.09%, #B415FF 106.28%);
    font-size: 22px;
    padding: 20px 60px;
    margin-bottom: 50px;
    cursor: pointer;
    transition: 0.3s;
}

.contact-submit:hover {
    transform: scale(1.1);
    transition: 0.3s;
}

@media (max-width: 480px) {

    .contact-title {
        text-align: center;
    }
    .contact-title h1 {
        font-size: 30px;
        width: 300px;
        text-align: center;
    }

    .contact-title img {
        width: 80px;
        right: 140px;
        top: 40px;
    }

    .contact{
        margin: 0px;
    }

    .contact-left {
        padding: 30px;
    }

    .contact-left h1 {
        font-size: 26px;
    }

    .contact-left p {
        font-size: 16px;
    }

    .contact-right {
        padding: 30px;
        padding-top: 0px;
    }

    .contact-right textarea {
        width: 320px;
    }

    .contact-right input {
        height: 60px;
    }

}