.hero {
    display: flex;
    align-items: center;
    flex-direction: column;
    gap:35px;
}

.hero img {
    margin-top: 60px;
}

.hero h1 {
    text-align: center;
    width: 70%;
    font-size: 90px;
    font-weight: 600;
}

.hero h1 span {
    background: linear-gradient(270deg, #DF8908, #B415FF 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

}

.hero p {
    width: 50%;
    text-align: center;
    font-size: 24px;
    line-height: 40px;
}

.hero-action {
    display:flex ;
    align-items: center;
    gap: 25px;
    font-size: 24px;
    font-weight: 500;
    margin-bottom: 50px
}

.hero-connect {
    padding: 25px 45px;
    border-radius: 50px;
    background: linear-gradient(270deg, #DF8908, #B415FF 100%);
    cursor: pointer;
}

.hero-resume {
    padding: 25px 75px;
    border-radius: 50px;
    border: 2px solid white;
    cursor: pointer;
}

.hero-connect:hover{
    border: 2px solid white;
}

.hero-resume:hover{
    border-color: #B415FF;
}



@media (max-width: 480px) {
    .hero img {
        width: 300px;
    }

    .hero h1 {
        font-size: 40px;
    }

    .hero p {
        font-size: 20px;
    }

    .hero-connect {
        height: 14px;
        width: 130px;
        font-size: 16px;
        padding: 20px;
    }

    .hero-resume {
        height: 14px;
        width: 90px;
        font-size: 16px;
        padding: 20px;
        padding-left: 40px;
    }
}